.selected-data-show{
  text-align: center;
}
.header{
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 10px;
}
.select-calendar{
  padding: 5px;
  width: 30%;
  background-color: rgba(0,0,0,.03);
  border: 1px solid #fff;
}
.btn-stap{
  background-color: rgba(0,0,0,.03);
  border: 1px solid #fff;
  cursor: pointer;
  padding: 10px 20px;
  &:hover{
    background-color: rgb(189, 190, 192);;
  }
}
.td-date{
  cursor: pointer;
  &:hover{
    background-color: rgb(243, 245, 237);
  }
}
.today{
  background-color: rgb(78, 75, 224);
  color: #fff;
}
.selected{
  background-color: rgb(243, 245, 237);
  border: 1px solid #000;
  box-shadow: 3px 5px 7px #ccc;
  color: #000;
  font-weight: bold;
}