.photos__row{
  margin-top: 15px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.img-thumbnail{
  margin: 10px;
  transition: 0.3s;
}
.img-thumbnail:hover{
  cursor: pointer;
  box-shadow: 7px 9px 11px rgb(145, 145, 151);
}
