.navbar{
  width: 80%;
  margin: 0 auto;
  border-radius: 5px;
}
.row{
  margin: 5px 0;
  padding: 5px;
  width: 100%;
}
.link-menu{
  display: block;
  text-align: center;
}
.advanced-menu .col-sm{
 padding-top: 5px;
}
.active{
  font-weight: bold;
  color: gold;
}
nav a{
  color: #fff;
}
nav a:hover{
  color: rgb(241, 234, 194);
  text-decoration: none;
}
.nav-links{
  display: flex;
  width: 100%;
  justify-content: space-around;
}