.comment{
  position: relative;
  margin-top: 15px;
  padding: 10px 10px;
  text-align: center;
  font-style: italic;
  border: 0.3px solid #ccc;
  background-color: rgb(244, 244, 245);
  box-shadow: 3px 5px 7px #eee;
}
.comment:before{
  content: open-quote;
  top: 5px;
  left: 10px;
}
.comment::after{
  content: close-quote;
  bottom: 10px;
  right: 5px;
}
.card-footer{
  margin: 10px 0px;
}
.comment:before,
.comment:after{
  font-size: 19px;
  line-height: 25px;
  background-color: #ccc;
  display: block;
  width: 15px;
  height: 15px;
  border-radius: 100%;
  text-align: center;
  position: absolute;
}

